
.cm-modal-enter {
  transform: scale(0.5);
  opacity: 0;
}
.cm-modal-enter-active {
  transition: 0.5s ease;
}
.cm-modal-enter-to {
  transform: scale(1);
  opacity: 1;
}
.cm-fade-enter {
  opacity: 0;
}
.cm-fade-enter-active {
  transition: 0.3s ease;
}
.cm-fade-enter-to {
  opacity: 1;
}
.cm-modal-container {
  position: fixed;
  z-index: 10600;
  right: 0;
  top: 0;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 40px 0;
  background: rgba(0, 0, 0, 0.4);
}
.cm-modal {
  position: relative;
  width: 500px;
  max-width: 90%;
  box-shadow: 0 0.5rem 2rem rgba(0, 0, 0, 0.3) !important;
  margin: auto;
  color: #444444;
  border-radius: .15rem;
  padding: 0;
  border: none;
  background-color: #ecf0f1;
}
.darkness .cm-modal {
    background-color: #282c34;
}
.cm-modal__close {
    position: absolute;
    z-index: 9;
    opacity: 1;
    top: 0;
    right: 0;
    width: 1.5rem;
    height: 1.5rem;
    line-height: 1.4rem;
    color: #ffffff;
    background-color: #e5a2a0;
    text-align: center;
    font-weight: 100;
}
.darkness .cm-modal__close {
      opacity: 0.64;
      background-color: #ca000c;
      font-family: serif;
}
.cm-modal__header {
    position: relative;
    height: 1.5rem;
    margin: 0;
    padding: 0 1.5rem 0 .5rem;
    line-height: 1.5rem;
    font-size: .9rem;
    border-bottom: 1px solid #dedede;
}
.cm-modal__header .fa {
      margin-right: 0.5em;
}
.darkness .cm-modal__header {
      border-bottom: 1px solid #17191d;
      color: #eeeeee;
}
.cm-modal__body {
    padding: 1rem;
}
